import React, { Component } from 'react'
import { SubmitStyle, SubminContent } from './SubmitStyle'
import { withRouter } from 'react-router'
import Title from '../Title/Title'
import Text from '../Text/Text'
import Bottom from '../Bottom/Bottom'

import TestModel from '../../models/test'
import LessonModel from "../../models/lesson";
import { observer } from 'mobx-react'

class Submit extends Component {
    constructor(props) {
        super(props)
        this.state = {
            test: null,
            lesson: null
        }
    }

    componentWillMount() {
        let id = this.props.match.params.test_id
        let test = TestModel.appliedTests.find(o => o.id == id)

        let lesson = LessonModel.lesson

        this.setState({
            test, lesson
        })
    }

    nextPath(path) {
        if (path !== this.props.location.pathname)
            this.props.history.push(path)
    }

    render() {
        let { test } = this.state
        let { questionsData, usersAnswers } = test
        return (
            <>
                <SubmitStyle>
                    <div className='test-header'>
                        <Title title={`${this.state.lesson.name} ${this.state.lesson.language}`} main />
                        <div className='notification'>
                            <Text size='small'>–¢–µ—Å—Ç –æ–∫–æ–Ω—á–µ–Ω. –í—ã –Ω–∞–±—Ä–∞–ª–∏ {test.progress}%</Text>
                            {test.progress === 100 ?
                                <Text size='small'>–û—Ç–ª–∏—á–Ω—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç, –ø—Ä–æ—Ö–æ–¥–∏—Ç–µ –±–æ–ª—å—à–µ —Ç–µ—Å—Ç–æ–≤ –∏ –∑–∞–∫—Ä–µ–ø–ª—è–π—Ç–µ –∑–Ω–∞–Ω–∏—è!</Text>
                                :
                                <Text size='small'>–•–æ—Ä–æ—à–∞—è –ø–æ–ø—ã—Ç–∫–∞, –∑–∞–ø–æ–º–Ω–∏—Ç–µ –ø—Ä–∞–≤–∏–ª—å–Ω—ã–µ –æ—Ç–≤–µ—Ç—ã –Ω–∏–∂–µ –∏ –Ω–∞—á–∏–Ω–∞–π—Ç–µ –Ω–æ–≤—ã–π —Ç–µ—Å—Ç</Text>
                            }
                        </div>
                    </div>
                    <div className='submit-content'>
                        {
                            test.wrongAnswers.map((data, i) => (
                                <>
                                    <SubminContent>
                                        <div className='card'>
                                        <div className='question'>
                                            <Text size='small' type='secondary'>–í–æ–ø—Ä–æ—Å {data + 1} –∏–∑ {questionsData.length}</Text>
                                            {questionsData[data].question}
                                        </div>
                                        <div className='answer'>  
                                            <Text type='secondary'>–í–∞—à –æ—Ç–≤–µ—Ç:</Text>                                            
                                            <Text type='secondary'>{usersAnswers[data]}</Text>
                                        </div>
                                        <div className='correct'>
                                            <Text>–ü—Ä–∞–≤–∏–ª—å–Ω—ã–π –æ—Ç–≤–µ—Ç:</Text>
                                            {questionsData[data].answer}
                                        </div>
                                        </div>
                                    </SubminContent>
                                </>
                            ))


                        }
                    </div>
                </SubmitStyle>
                <Bottom type='end-test' />
            </>
        )
    }
}

export default withRouter(observer(Submit))